/* Video Player Page Styles */
.video-player-page {
  position: fixed;
  inset: 0;
  background-color: var(--color-black);
  overflow: hidden;
}

.video-player {
  position: relative;
  width: 100%;
  height: 100%;
}

.video-player__container {
  position: absolute;
  inset: 0;
  overflow: hidden;
}

/* Desktop: Cover behavior - target Vimeo-generated iframe */
.video-player__container iframe {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
  /* Dimensions are set dynamically via JS based on video aspect ratio */
}

/* Thumbnail placeholder - shows while Vimeo loads */
.video-player__poster {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  z-index: 2;
  opacity: 1;
  transition: opacity 0.5s ease-out;
}

.video-player__poster--hidden {
  opacity: 0;
  pointer-events: none;
}

/* Clickable overlay for play/pause */
.video-player__click-area {
  position: absolute;
  inset: 0;
  z-index: 5;
  cursor: pointer;
}

/* Controls Overlay */
.video-player__controls {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  padding: var(--gutter);
  background: linear-gradient(to top, rgba(0, 0, 0, 0.6) 0%, transparent 100%);
  opacity: 0;
  transition: opacity var(--duration-normal) var(--ease-out);
}

.video-player:hover .video-player__controls,
.video-player--controls-visible .video-player__controls {
  opacity: 1;
}

.video-player__controls-inner {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.video-player__controls-inner-left {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  gap: var(--space-10);
}

/* Project Info */
.video-player__info {
  display: flex;
  flex-direction: row;
  gap: var(--gutter);
  letter-spacing: 0.5;
}

.video-player__info-project {
  display: flex;
  flex-direction: column;
  gap: var(--space-1);
}

.video-player__number {
  color: var(--color-text);
  opacity: 0.6;
}

.video-player__title {
  color: var(--color-text);
}

.video-player__client {
  color: var(--color-text);
  opacity: 0.6;
}

/* Control Buttons */
.video-player__buttons {
  display: flex;
  align-items: center;
  gap: var(--space-3);
}

.video-player__button {
  color: var(--color-text);
  opacity: 0.8;
  transition: opacity var(--duration-fast) var(--ease-out);
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}

.video-player__button:hover {
  opacity: 1;
}

/* Fullscreen button - mobile only */
.video-player__button--fullscreen {
  display: none;
}

/* Progress Bar */
.video-player__progress {
  flex: 1;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  gap: var(--space-3);
}

.video-player__progress-bar {
  flex: 1;
  height: 8px;
  background-color: rgba(255, 255, 255, 0.3);
  cursor: pointer;
  position: relative;
}

.video-player__progress-fill {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-color: var(--color-white);
  transition: width 300ms ease-in-out;
}



/* Close Button */
.video-player__close {
  color: var(--color-text);
  opacity: 0;
  transition: opacity var(--duration-normal) var(--ease-out);
  cursor: pointer;
  background: none;
  border: none;
  padding: 0;
}

.video-player:hover .video-player__close,
.video-player--controls-visible .video-player__close {
  opacity: 0.8;
}

.video-player__close:hover {
  opacity: 1;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  /* Mobile: Contained behavior - video fits within viewport */
  .video-player__container {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .video-player__container iframe {
    position: relative;
    top: auto;
    left: auto;
    transform: none;
    width: 100% !important;
    height: auto !important;
    aspect-ratio: var(--video-aspect-ratio, 16/9);
  }

  .video-player__controls {
    padding: var(--space-6) var(--space-5);
  }

  .video-player__controls-inner {
    flex-wrap: wrap;
    gap: var(--space-3);
  }

  .video-player__controls-inner-left {
    gap: 10%;
  }

  .video-player__info {
    min-width: auto;
    width: 100%;
    order: 1;
    gap: var(--gutter);
  }

  .video-player__number {
    font-size: 0.625rem;
  }

  .video-player__title {
    font-size: 0.625rem;
    white-space: nowrap;
  }

  .video-player__client {
    font-size: 0.625rem;
  }

  .video-player__buttons {
    order: 3;
    gap: var(--space-3);
  }

  .video-player__button--fullscreen {
    display: block;
  }

  .video-player__button {
    font-size: 0.625rem;
  }

  .video-player__button.play {
    display: none;
  }

  .video-player__progress {
    order: 2;
    width: 100%;
  }

  .video-player__close {
    bottom: auto;
    top: var(--space-3);
    right: var(--space-3);
    font-size: 0.625rem;
  }
}

@media (max-width: 480px) {
  .video-player__info {
    gap: var(--gutter);
  }
}

